.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    background-color: rgba(0, 0, 0, .6);
    opacity: 0;
    transition: all ease-in .2s;
}

.modal__content {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) scale(0);
            transform: translate(-50%, -50%) scale(0);
    background-color: white;
    box-shadow: 0 1em 3em rgba(0, 0, 0, .8);
    border-radius: 2px;
    padding: 25px;
    transition: all ease-in .2s .2s;


}

.show {
    opacity: 1;
    width: 100%;
    height: 100%;
}

.show>.modal__content {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
}


.close-button {
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
